<template>
	<div></div>
</template>

<script>
import { defineComponent } from '@vue/composition-api';

export default defineComponent({
	'name': 'NotificationEventSnackbar',
	
	'methods': {
		markAsArchived(notification) {
			this.$store.dispatch('notifications/set', {
				'notificationId': notification.id,
				'props': {
					'state': 'archived'
				}
			});
		},
		markAsRead(notification) {
			this.$store.dispatch('notifications/set', {
				'notificationId': notification.id,
				'props': {
					'state': 'read'
				}
			});
		},
		remove(notification) {
			this.$store.dispatch('notifications/del', {
				'notificationId': notification.id
			});
		},
		
		/**
		 * @see https://quasar.dev/quasar-plugins/notify
		 */
		show(notification = {}) {
			// backwards compatibility
			if (!notification.type && notification.criticality) {
				notification.type = notification.criticality === 'error' ? 'negative' : (notification.criticality === 'success' ? 'positive' : notification.criticality); // optional, one of none, error, warning, info or success (default is none)
			}
			
			if (!notification.position && notification.snackbarOrigin) {
				notification.position = [];
				
				if (notification.snackbarOrigin.horizontal) {
					notification.position.push(notification.snackbarOrigin.horizontal);
				}
				
				if (notification.snackbarOrigin.vertical) {
					notification.position.push(notification.snackbarOrigin.vertical);
				}
				
				notification.position = notification.position.join(notification.position.length === 2 ? '-' : '');
			}
			
			this.$q.notify({
				//'type': 'positive', // Optional type (that has been previously registered) or one of the out of the box ones ('positive', 'negative', 'warning', 'info', 'ongoing')
				'color': 'primary', // Color name for component from the Quasar Color Palette
				'textColor': 'white', // Color name for component from the Quasar Color Palette
				//'caption': '', // The content of your optional caption
				'html': true, // Render message as HTML; This can lead to XSS attacks, so make sure that you sanitize the message first
				//'icon': 'mail', // Icon name following Quasar convention; Make sure you have the icon library installed unless you are using 'img:' prefix
				'position': 'bottom-right', // Window side/corner to stick to top-left, top-right, bottom-left, bottom-right, top, bottom, left, right, center
				'group': false, // Override the auto generated group with custom one; Grouped notifications cannot be updated; String or number value inform this is part of a specific group, regardless of its options; When a new notification is triggered with same group name, it replaces the old one and shows a badge with how many times the notification was triggered
				'progress': true, // Show progress bar to detail when notification will disappear automatically (unless timeout is 0)
				'progressClass': 'jarvis-notification-event-snackbar-progress', // Class definitions to be attributed to the progress bar
				'timeout': notification.autoHideDuration || 5000, // Amount of time to display (in milliseconds)
				'classes': 'jarvis-notification-event-snackbar', // Add CSS class(es) to the notification for easier customization
				'actions': [
					{ 'icon': 'mdi-circle-slice-8', 'size': 'sm', 'color': notification.textColor || 'white', 'handler': () => this.markAsRead(notification) },
					{ 'icon': 'mdi-package-down', 'size': 'sm', 'color': notification.textColor || 'white', 'handler': () => this.markAsArchived(notification) },
					{ 'icon': 'mdi-delete', 'size': 'sm', 'color': 'negative', 'handler': () => this.remove(notification) }
				],
				...notification, // https://github.com/Zefau/ioBroker.jarvis/issues/56
				'message': notification.title || notification.message // The content of your message
			});
		}
	},
	
	created() {
		this.$store.subscribe(mutation => {
			if (mutation.type === 'notifications/setNew') {
				const notifications = mutation.payload.notifications;
				notifications.forEach(notification => notification.display === 'snackbar' && this.show(notification));
				this.$store.commit('notifications/resetNew');
			}
		});
	}
});
</script>
