<template>
	<q-drawer @input="toggleDrawerRight(false)" :value="drawerRight" side="right" behavior="mobile" :width="500" overlay bordered content-class="NotificationDrawer">
		<q-header>
			<q-toolbar>
				<h6>
					<icon name="mdi-bell" />
					{{ $t('Notifications') }}
				</h6>
				
				<q-space />
				<q-btn flat dense round icon="mdi-chevron-right" aria-label="Menu" @click="toggleDrawerRight(false)" />
			</q-toolbar>
		</q-header>
		
		<notification-list />
	</q-drawer>
</template>

<script>
import { defineComponent } from '@vue/composition-api';

import NotificationList from './List.vue';

import i18n from './i18n';
export default defineComponent({
	'name': 'NotificationDrawer',
	
	'components': {
		NotificationList
	},
	
	'methods': {
		toggleDrawerRight(val) {
			this.$store.commit('jarvis/set', {
				'drawerRightOpen': val !== undefined ? val : !this.drawerRight
			});
		}
	},
	
	'computed': {
		drawerRight() {
			return this.$store.state.jarvis.drawerRightOpen;
		}
	},
	
	created() {
		i18n.register(this.$i18n, i18n.translations);
	}
});
</script>

<style lang="scss">
.NotificationDrawer {
	background-color: $grey-1;
	margin-top: 40px;
}
</style>
