<template>
	<div>
		<q-dialog
			v-for="notification in notifications"
			:key="notification.id"
			:value="dialogs[notification.id]"
			@hide="dialogs[notification.id] = false"
			transition-show="scale"
			transition-hide="scale"
			class="jarvis-notification-event-popup"
			>
			
			<q-card
				:class="{ ['bg-' + (notification.color || notification.type || 'primary')]: true, ['text-' + (notification.textColor || 'white')]: true }"
				style="width: 500px; max-width: 80vw;"
				>
				
				<q-card-section v-if="notification.title || notification.caption">
					<div class="text-h6">{{ notification.title }}</div>
					<div v-if="notification.caption" class="text-caption">{{ notification.caption }}</div>
				</q-card-section>
				
				<q-card-section v-if="notification.message" class="q-pt-none">
					<div v-html="notification.message" />
				</q-card-section>
				
				<q-card-section v-if="notification.progress" class="q-pa-none" :class="{ ['updated-' + refresh]: true, ['text-' + (notification.color || notification.type || 'primary')]: true, ['bg-' + (notification.textColor || 'white')]: true }">
					<q-linear-progress :value="timers[notification.id] ? timers[notification.id] / notification.timeout : 0" class="q-mt-md" :color="notification.color || notification.type || 'primary'" />
				</q-card-section>
				
				<q-card-actions :class="{ ['text-' + (notification.color || notification.type || 'primary')]: true, ['bg-' + (notification.textColor || 'white')]: true }">
					<q-btn flat :label="$t('Delete')" icon="mdi-delete" color="negative" @click="remove(notification)" v-close-popup />
					<q-space />
					
					<q-btn flat :label="$t('Archive')" icon="mdi-package-down" @click="markAsArchived(notification)" v-close-popup />
					<q-btn flat :label="$t('Mark as Read')" icon="mdi-circle-slice-8" @click="markAsRead(notification)" v-close-popup />
				</q-card-actions>
			</q-card>
		</q-dialog>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api';

export default defineComponent({
	'name': 'NotificationEventPopup',
	
	data() {
		return {
			'refresh': 0,
			'dialogs': {},
			'timers': {},
			'notifications': []
		}
	},
	
	'methods': {
		markAsArchived(notification) {
			this.$store.dispatch('notifications/set', {
				'notificationId': notification.id,
				'props': {
					'state': 'archived'
				}
			});
		},
		markAsRead(notification) {
			this.$store.dispatch('notifications/set', {
				'notificationId': notification.id,
				'props': {
					'state': 'read'
				}
			});
		},
		remove(notification) {
			this.$store.dispatch('notifications/del', {
				'notificationId': notification.id
			});
		},
		timer(notification, step = 50) {
			setTimeout(() => {
				this.$set(this.timers, notification.id, Math.max(0, this.timers[notification.id] - step));
				this.refresh = Date.now();
				
				if (this.timers[notification.id] > 0) {
					this.timer(notification);
				}
				else {
					this.$set(this.dialogs, notification.id, false);
				}
			}, step);
		}
	},
	
	created() {
		this.$store.subscribe(mutation => {
			if (mutation.type === 'notifications/setNew') {
				this.notifications = mutation.payload.notifications.filter(notification => notification.display === 'popup');
				this.notifications.forEach(notification => {
					if (notification.progress) {
						notification.timeout = notification.timeout || 5000;
						this.timers[notification.id] = notification.timeout;
						this.timer(notification, 500);
					}
					
					this.$set(this.dialogs, notification.id, true);
				});
				
				this.$store.commit('notifications/resetNew');
			}
		});
	}
});
</script>
